  outline: none;
}

.slider__arrow:focus, .slider__arrow:hover {
  outline: none;
}

.slider__arrow.swiper-button-disabled {
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
}

.slider__arrow_left {
  top: 50%;
  transform: translateY(-50%);
  left: var(--gutter-horizontal);
}

.slider__arrow_right {
  top: 50%;
  transform: translateY(-50%);
  right: var(--gutter-horizontal);
}

.swiper-lazy {
  max-width: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.swiper-lazy-loaded {
  opacity: 1;
  visibility: visible;
}

.slider__wrapper-canvas, .slider__wrapper-canvas-inner {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.slider__canvas {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  transform: translate(-50%, -50%);
}

.slider__images-slide-inner_circle {
  width: calc(100vh - var(--gutter-horizontal) * 2.5);
  height: calc(100vh - var(--gutter-horizontal) * 2.5);
