.slider__dot:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 100%;
  background-color: rgba(104, 104, 104, 0.5);
