  padding-right: 6px;
  position: relative;
}

.block-counter__counter {
  position: absolute;
  top: 0;
  left: 100%;
  font-size: calc(13 * 1px);
  font-family: var(--font-secondary);
  font-weight: bold;
  color: var(--color-gray-1);
  white-space: nowrap;
}

@media screen and (min-width: 320px) {
  .block-counter__counter {
    font-size: calc(13 * 1px + (16 - 13) * ((100vw - 320px) / 2240));
