/* ======================================================================== */
/* 51. parallax */
/* ======================================================================== */
@for $i from 1 through 9
	[data-scroll-speed='-#{1 + $i / 10}'], [data-scroll-speed='#{1 + $i / 10}']
		.lazy-bg, .lazy
			transform: scale(#{1 + $i / 40})
			transform-origin: center center
