	/* h2 heading */
	--h2-max-font-size: 65;
	--h2-min-font-size: 31;
	--h2-line-height: 1.31;
	--h2-color-light: #eeece6;
	--h2-color-dark: #333333;
	/* h3 heading */
	--h3-max-font-size: 42;
	--h3-min-font-size: 24;
	--h3-line-height: 1.29;
	--h3-color-light: #eeece6;
	--h3-color-dark: #333333;
	/* h4 heading */
	--h4-max-font-size: 26;
	--h4-min-font-size: 22;
	--h4-line-height: 1.62;
	--h4-color-light: #ffffff;
	--h4-color-dark: #262626;
	/* h5 heading */
	--h5-max-font-size: 18;
	--h5-min-font-size: 18;
	--h5-line-height: 1.6;
	--h5-color-light: #ffffff;
	--h5-color-dark: #262626;
	/* h6 heading */
	--h6-max-font-size: 14;
