	--blockquote-color-light: #ffffff;
	--blockquote-color-dark: #262626;
	/* Paragraph */
	--paragraph-max-font-size: 18;
	--paragraph-min-font-size: 16;
	--paragraph-line-height: 1.8;
	--paragraph-color-light: #cccccc;
	--paragraph-color-dark: #262626;
	/* Dropcap */
	--dropcap-max-font-size: 110;
	--dropcap-min-font-size: 60;
	--dropcap-line-height: 0.7;
	--dropcap-color-light: #ffffff;
	--dropcap-color-dark: #111111;
	/* Fluid paddings & margins (min values) */
	--distance-min-xsmall: 30;
	--distance-min-small: 30;
	--distance-min-medium: 50;
	--distance-min-large: 100;
	--distance-min-xlarge: 160;
	/* Fluid paddings & margins (max values) */
	--distance-max-xsmall: 50;
	--distance-max-small: 120;
	--distance-max-normal: 240;
	--distance-max-large: 360;
	--distance-max-xlarge: 400;
	/* Container & page gutters */
	--gutter-horizontal: 120px;
	--gutter-vertical: 80px;
	@media screen and (max-width: $xxl) {
		--gutter-horizontal: 80px;
		--gutter-vertical: 60px; }
	@media screen and (max-width: $xl) {
		--gutter-horizontal: 60px;
		--gutter-vertical: 40px; }
	@media screen and (max-width: $md) {
		--gutter-horizontal: 20px;
		--gutter-vertical: 20px; }
	/* preloader circle */
	--preloader-circle-max-size: 960;
	--preloader-circle-min-size: 280; }

html {
	font-size: $em-base;
	-webkit-font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
	-webkit-text-rendering: optimizeLegibility;
	-moz-osx-font-smoothing: grayscale;
	font-kerning: none; }
body {
	position: relative;
	width: 100%;
	min-width: 320px;
	font-family: $font-primary;
	font-size: 100%;
	line-height: $line-height;
	color: var(--paragraph-color-dark);
	word-wrap: break-word; }
.body_lock-scroll {
	overflow: hidden;
	position: fixed;
	position: sticky;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%; }
img, iframe {
	max-width: 100%;
	height: auto; }
a {
	@include trans1;
	text-decoration: none;
	color: var(--color-gray-1);
	&:hover {
		color: var(--color-dark-2);
		text-decoration: none; } }
.page-wrapper__content {
	position: relative;
	overflow: hidden;
	z-index: 50; }

/* ======================================================================== */
/* 1. arrow */
/* ======================================================================== */
.arrow {
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	vertical-align: middle;
	cursor: pointer;
	outline: none;
	.svg-circle {
		width: 60px;
		height: 60px; }
	.circle {
		stroke: $borders-dark-2;
		stroke-width: 1px; }
	&:hover {
		.arrow__pointer {
			background-color: var(--color-dark-2); }
		.circle {
			stroke: var(--color-dark-2); }
		.arrow__triangle {
			border-color: transparent transparent transparent var(--color-dark-2); } } }

.arrow_mini {
	.svg-circle {
		width: 40px;
		height: 40px; }
	.arrow__pointer {
		width: 50% !important;
		right: 22px; }
	.arrow__triangle {
		border-width: 4px 0 4px 6px; }
	.circle {
		stroke-width: 2px; } }

.js-arrow {
	&:hover {
		.arrow__pointer {
			transform: scaleX(0.3) translateX(24px);
			width: 100% !important; }
		.arrow__triangle {
			transform: translate(calc(-50% + 6px), -50%); } } }

.arrow-left {
	transform: rotate(180deg); }

.arrow__pointer {
	display: block;
	position: absolute;
	width: 100%;
	height: 2px;
	right: 30px;
	top: 0;
	bottom: 0;
	margin: auto;
	background-color: $borders-dark-2;
	@include trans1;
	transform-origin: right center; }

.arrow__triangle {
	display: inline-block;
	border-style: solid;
	border-width: 6px 0 6px 8px;
	border-color: transparent transparent transparent $borders-dark-2;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	@include trans1; }

.arrow-up {
	transform: rotate(-90deg);
	.arrow__pointer {
		width: 80%; } }

.arrow-down {
	transform: rotate(90deg);
	.arrow__pointer {
		width: 80%; } }

@media screen and (max-width: $md) {
	.arrow {
		width: 36px;
		height: 36px;
		.svg-circle {
			width: 36px;
			height: 36px; }
		.arrow__pointer {
			width: 50% !important;
			right: 20px;
 }			// top: 2px
		.arrow__triangle {
			top: 50%;
			border-width: 4px 0 4px 6px; }
		.circle {
			stroke-width: 2px; } } }

/* ======================================================================== */
/* 2. asideCounters */
/* ======================================================================== */
.aside-counters_2, .aside-counters_3, .aside-counters_4 {
	.aside-counters__wrapper-item {
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 10px;
			bottom: 10px;
			right: 0;
			width: 1px;
			background-color: $borders-dark; } } }

.aside-counters_2 {
	.aside-counters__wrapper-item {
		&:nth-child(2) {
			&:after {
				display: none; } }
		&:last-child {
			&:after {
				display: none; } } } }

.aside-counters_3 {
	.aside-counters__wrapper-item {
		&:nth-child(3) {
			&:after {
				display: none; } }
		&:last-child {
			&:after {
				display: none; } } } }

.aside-counters_4 {
	.aside-counters__wrapper-item {
		&:nth-child(4) {
			&:after {
				display: none; } }
		&:last-child {
			&:after {
				display: none; } } } }

@media only screen and (max-width: $md) {
	.aside-counters__wrapper-item {
		&:after {
			top: 0;
			bottom: 0; } }
	.aside-counters_3 {
		.aside-counters__wrapper-item {
			&:nth-child(2) {
				&:after {
					display: none; } } } }
	.aside-counters_4 {
		.aside-counters__wrapper-item {
			&:nth-child(2) {
				&:after {
					display: none; } } } } }

