/* ======================================================================== */
/* 5. button */
/* ======================================================================== */
.button {
	position: relative;
	display: inline-block;
	@include small-caps;
	font-weight: bold;
	text-align: center;
	@include trans1;
	cursor: pointer;
	outline: none;
	box-shadow: none;
	border-width: 2px;
	border-style: solid;
	padding: 21px 48px;
	border-radius: 64px;
	&:focus {
		outline: none; } }

.button[data-hover] {
	&:before {
		content: attr(data-hover);
		display: block;
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		transform: translateY(100%);
		@include trans1;
		opacity: 0;
		visibility: hidden; }
	&:hover {
		.button__label-hover {
			transform: translateY(-100%);
			opacity: 0;
			visibility: hidden; }
		&:before {
			transform: translateY(-50%);
			opacity: 1;
			visibility: visible; } } }

.button__label-hover {
	display: block;
	transform: translateY(0%);
	@include trans1;
	opacity: 1;
	visibility: visible; }

.button_icon {
	display: inline-flex;
	padding: 0;
	border: none; }

.button__label {
	display: inline-block;
	padding: 21px 48px; }

.button__icon {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	width: 60px;
	@include trans1;
	i {
		font-size: 24px; } }

.button_fullwidth {
	width: 100%; }


@media screen and (max-width: 1680px) {
	.button {
		padding: 14px 32px; }
	.button__label {
		padding: 18px 32px; } }

@media screen and (max-width: $md) {
	.button {
		padding: 12px 28px; }
	.button__label {
		padding: 12px 28px; }
	.button__icon {
		width: 43px;
		i {
			font-size: 20px; } }
	.button_icon {
		padding: 0; } }

/* ======================================================================== */
/* 6. buttonBordered */
/* ======================================================================== */
.button_bordered {
	&.button_white {
		border-color: #fff;
		color: #fff; }
	&.button_black {
		border-color: $black;
		color: $black; } }

/* ======================================================================== */
/* 7. buttonSolid */
/* ======================================================================== */
.button_solid {
	&.button_black {
		border-color: $black;
		background-color: $black;
		color: #fff; } }

/* ======================================================================== */
/* 8. changeTextHover */
/* ======================================================================== */
.change-text-hover {
	position: relative;
	display: inline-block;
	white-space: nowrap;
	line-height: 1;
	padding-left: 5px;
	&.text-right {
		.change-text-hover__hover {
			left: auto;
			right: 0; } }
	&.text-lg-right {
		@media screen and (min-width: $md - 1) {
			.change-text-hover__hover {
				left: auto;
				right: 0; } } } }

.change-text-hover_line-visible {
	.change-text-hover__line {
		transform: scaleX(1) !important;
		transform-origin: 'left center' !important;
		@include trans1; }
	.change-text-hover__normal {
		transform: translateX(100%); } }

.change-text-hover__hover {
	display: inline-flex;
	align-items: center;
	position: absolute;
	top: 0; }

.change-text-hover__line {
	display: inline-block;
	width: 60px;
	height: 1px;
	background-color: $brown-grey;
	margin-right: 1em;
	transform: scaleX(0); }

/* ======================================================================== */
/* 9. circleButton */
/* ======================================================================== */
.js-circle-button[data-arts-os-animation] {
