/* ======================================================================== */
/* 35. headerLogo */
/* ======================================================================== */
[data-arts-header-logo="primary"] {
	.logo__img-primary {
		opacity: 1;
		visibility: visible; }
	.logo__img-secondary {
		opacity: 0;
		visibility: hidden; } }
