			color: var(--color-gray-2); } } }

/* ======================================================================== */
/* 37. headerThemes */
/* ======================================================================== */
.header[data-arts-theme-text="light"] {
	.logo__text-title {
		color: #fff; }
	a {
		&:hover {
			color: #fff; } }
	.header__burger-line {
		background-color: #fff; } }
.header {
	&.bg-dark-1, &.bg-dark-2, &.bg-dark-3, &.bg-dark-4 {
		.header__burger-line {
			background-color: #fff; } }
	&.bg-light-1, &.bg-light-2, &.bg-light-3, &.bg-light-4, &.bg-white {
		.header__burger-line {
			background-color: var(--color-gray-1); } } }
.header.opened[data-arts-header-overlay-theme="light"] {
	color: var(--color-dark-2);
	p, .paragraph {
		color: var(--color-dark-2); }
	.header__burger-line {
		background-color: var(--color-gray-1); }
	.header__burger {
		&:hover {
			.header__burger-line {
				background-color: var(--color-dark-2); } } }
	a {
		color: var(--color-gray-1);
		&:hover {
			color: var(--color-dark-1); } }
	.header__overlay-menu-back {
		color: var(--color-dark-2);
		&:hover {
			.arrow .circle {
				stroke: var(--color-dark-2); }
			.arrow__pointer {
				background-color: var(--color-dark-2); }
			.arrow__triangle {
				border-color: transparent transparent transparent var(--color-dark-2); } } } }
.header.opened[data-arts-header-overlay-theme="dark"] {
	color: #fff;
	.header__burger-line {
		background-color: var(--color-gray-1); }
	.header__burger {
		&:hover {
			.header__burger-line {
				background-color: #fff; } } }
	a {
		color: var(--color-gray-1);
		&:hover {
			color: #fff; } }
	.header__overlay-menu-back {
		.arrow .circle {
			stroke: var(--color-gray-1); }
		.arrow__pointer {
			background-color: var(--color-gray-1); }
		.arrow__triangle {
			border-color: transparent transparent transparent var(--color-gray-1); }
		&:hover {
			.arrow .circle {
				stroke: var(--color-gray-1); }
			.arrow__pointer {
				background-color: #fff; }
			.arrow__triangle {
				border-color: transparent transparent transparent #fff; } } } }

/* ======================================================================== */
/* 38. inputFloat */
/* ======================================================================== */
.input-float {
	position: relative;
	display: block;
	width: 100%;
	margin-bottom: 0; }
.input-float__label {
	position: absolute;
	top: 0.75em;
	left: 0;
	margin: auto;
	display: block;
	font-size: 16px;
	@include trans1;
	transform-origin: left center;
	cursor: text;
	color: $brown-grey; }
.input-float__input {
	display: block;
	width: 100%;
	width: 100%;
	border-bottom: 1px solid $very-light-pink;
	border-top: none;
	border-right: none;
	border-left: none;
	outline: none;
	padding: 10px 0 4px;
	@include trans1;
	background-color: transparent;
	border-radius: 0;
	color: inherit; }
.input-float__input_textarea {
