	top: 12px;
	bottom: 0;
	margin: auto;
	background: transparent;
	border: none;
	color: $brown-grey;
	outline: none;
	@include trans1;
	cursor: pointer;
	&:hover {
		color: $dark; }
	&:focus {
		outline: none; } }

/* ======================================================================== */
/* 41. lazy */
/* ======================================================================== */
.lazy {
	background-color: rgba(136, 136, 136, .1);
	> img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%; } }
.lazy_loaded {
